@font-face {
  font-family: Bungee;
  src: url("Bungee-Regular.3356ad6c.ttf");
}

@font-face {
  font-family: Dekko;
  src: url("Dekko-Regular.c9bdc07f.ttf");
}

:root {
  --padding: 20px;
  --margin: 20px;
  --radius: 10px;
  --background: #c5e7f2;
  --items: #ffffff61;
  --form: #f45d5d;
  --header: #fff0d9;
  --title: #253825;
}

* {
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  display: flex;
}

#header {
  background: var(--header);
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 50px;
  font-family: Bungee;
  display: flex;
}

#movingTopText {
  color: var(--header);
  padding: 5px;
  font-family: Bungee;
}

h1 {
  color: var(--title);
  font-size: 60px;
}

#form {
  background-color: var(--form);
  border-radius: 5px;
  flex-direction: row;
  align-items: center;
  width: 350px;
  height: 50px;
  display: flex;
  position: relative;
}

#searchInput {
  all: unset;
  color: #fff;
  width: 100%;
  height: 100%;
  padding: 6px 10px;
  font: 25px system-ui;
}

#searchButton {
  color: var(--header);
  background: none;
  border: none;
  width: 50px;
  font-size: 30px;
}

#searchButton:hover {
  color: #ff9b9b;
}

#shoppingCart {
  width: 100px;
}

#categoryContainer {
  background-color: var(--header);
  color: var(--title);
  padding-bottom: var(--padding);
  justify-content: center;
  font-family: Bungee;
  display: flex;
}

.categoryDiv {
  justify-content: space-between;
  width: 70%;
  font-size: 1.5vw;
  display: flex;
}

.categoryHover:hover {
  color: #97b38f;
}

.productCard {
  background-color: var(--background);
  color: var(--title);
  height: 100vhs;
  background-attachment: fixed;
  flex-flow: wrap;
  justify-content: center;
  padding: 20px;
  display: flex;
}

.product {
  padding: var(--padding);
  margin: var(--margin);
  box-sizing: border-box;
  background-color: var(--items);
  border-radius: var(--radius);
  width: 300px;
  height: 700px;
  line-height: 30px;
  position: relative;
}

.pFont {
  font-family: Dekko;
}

.cartButton {
  background-color: var(--form);
  border-radius: var(--radius);
  color: var(--header);
  bottom: var(--padding);
  border: none;
  padding: 5px;
  display: flex;
  position: absolute;
}

.cartButton:hover {
  background-color: #ff9b9b;
}

.product > img {
  border-radius: var(--radius);
  width: 250px;
}

.productTitle {
  margin: 20px 0;
  font-family: Bungee;
}

.lowstock {
  color: var(--form);
  font-weight: bold;
}
